// Single page/post/project

// - - - - - - - - - - - - - - - - - -

.intro {
	
	h1 {
		
		@include mq(tabletp) {
			white-space: pre-line;
		}
	}

	p {

	}
}

.content-wrap {

	@include mq(tabletp) {
		max-width: 90%;
		margin: 0 auto;
	}
}

.single {

	h1, h2, h3, h4, h5, h6, p, ul, ol {
		margin-left: auto;
		margin-right: auto;
		padding-left: 30px;
		padding-right: 30px;
		max-width: 500px;

		@include mq(tabletp) {
			padding-left: 0;
			padding-right: 0;
			max-width: 70%;
		}

		@include mq(tabletl) {
			max-width: 65%;
		}

		@include mq(laptop) {
			max-width: 60%;
		}
	}

	hr {
		margin: 30px auto;
		width: calc(100vw - 60px);
		max-width: 440px;

		@include mq(tabletp) {
			margin-top: 40px;
			margin-bottom: 40px;
			margin-left: auto;
			margin-right: auto;
			width: auto;
			max-width: 70%;
		}

		@include mq(tabletl) {
			max-width: 65%;
		}

		@include mq(laptop) {
			max-width: 60%;
		}
	}

	blockquote,
	figure {
		max-width: 90%;
		margin: 20px auto;

		@include mq(tabletp) {
			margin: 30px auto;
		}

		@include mq(laptop) {
			margin: 40px auto;
		}
	}

	blockquote p {
		max-width: 100%;
		padding: 0;
	}

	p a {
		font-weight: $bold-weight;
		border-bottom: 2px solid $accent-color;
	}

	p .button {
		margin-top: 10px;
	}

	.highlighter-rouge {
		margin: 20px auto;
		width: 100%;
		max-width: 90%;

		@include mq(tabletp) {
			margin: 30px auto;
			max-width: 70%;
		}

		@include mq(tabletl) {
			max-width: 65%;
		}

		@include mq(laptop) {
			margin: 40px auto;
			max-width: 60%;
		}
	}

	.highlight {
		display: block;
		padding: 20px;
		line-height: 1.5;

		@include mq(tabletp) {
			padding: 30px;
		}

		@include mq(laptop) {
			padding: 40px;
		}

		.highlight {
			padding: 0;
		}
	}
}

.image-wrap,
.video-wrap {
	position: relative;
	max-width: 90%;
	margin: 20px auto;

	@include mq(tabletp) {
		margin: 30px auto;
	}

	@include mq(laptop) {
		margin: 40px auto;
	}

	&:first-child {
		margin-top: 0;
	}
}

.image-wrap img {
	margin: 0 auto;
}

.video {
	position: relative;
	width: 100%;
	height: 0;

	iframe {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
}